      const idx =  index || ''
      if(this.isAndroid || this.isiOS) {
        this.SET_SCROLLINDECX(idx);
      }
      const warpLink = [
          {id0:'87510', id:'82093', eventName: 'access_solution_TRTC'},
          {id0:'87508', id:'82094', eventName: 'access_solution_ILVB'},
          {id0:'87507', id:'82095', eventName: 'access_solution_GME'}
        ]
      warpLink.forEach((item) => {
        if(id === item.id) {
          handleClickEvent(item.id0, item.eventName)
        }
      })
      handleClickEvent(id, eventName, params)
    },
    handleClickProjectTab(index) {
      this.projectCunrrent = index;
    }
  }
}
</script>

